body {
  background-color: #2B2B2B;
  color: #FFFFFF;
  font-family: 'Work Sans', sans-serif;
  font-weight: 400;

  .App {
    overflow-x: hidden;
  }

  main {
    max-width: 1280px;
    margin: 0 auto;

    h1 {
      font-family: 'Work Sans', sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 67px;
      line-height: 110%;
      text-transform: capitalize;
      margin-bottom: 20px;

      @media (max-width: 885px) {
        font-size: 38px;
      }
    }

    section {
      max-width: 1050px;
      margin: 0 auto 150px auto;

      @media (max-width: 1050px) {
        padding: 0 20px;
      }

      h4 {
        font-style: normal;
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
      }
    }
  }

}

